import React, { useState, useEffect } from 'react';
import { useLanguage } from '../contexts/LanguageContext';
import './NotificationSettings.css';

function NotificationSettings({ user }) {
  const [notifications, setNotifications] = useState({
    daily: true,
    weekly: false,
    email: false
  });
  const { t } = useLanguage();

  useEffect(() => {
    //    
    if ('Notification' in window) {
      //    
      const savedSettings = localStorage.getItem('notificationSettings');
      if (savedSettings) {
        const { enabled, time } = JSON.parse(savedSettings);
        setNotifications({
          daily: enabled,
          weekly: false,
          email: false
        });
      }
    }
  }, []);

  const handleChange = (setting) => {
    setNotifications(prev => ({
      ...prev,
      [setting]: !prev[setting]
    }));
  };

  const requestPermission = async () => {
    try {
      const result = await Notification.requestPermission();
      if (result === 'granted') {
        setNotifications({
          daily: true,
          weekly: false,
          email: false
        });
        saveSettings(true, '09:00');
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  const saveSettings = (enabled, time) => {
    const settings = { enabled, time };
    localStorage.setItem('notificationSettings', JSON.stringify(settings));
    
    if (enabled) {
      scheduleNotification(time);
    } else {
      cancelNotification();
    }
  };

  const scheduleNotification = (time) => {
    //   
    cancelNotification();

    //   
    const [hours, minutes] = time.split(':');
    const now = new Date();
    const scheduledTime = new Date(
      now.getFullYear(),
      now.getMonth(),
      now.getDate(),
      parseInt(hours),
      parseInt(minutes),
      0
    );

    //     
    if (scheduledTime < now) {
      scheduledTime.setDate(scheduledTime.getDate() + 1);
    }

    const timeUntilNotification = scheduledTime - now;
    const notificationId = setTimeout(() => {
      showNotification();
      //    
      scheduleNotification(time);
    }, timeUntilNotification);

    localStorage.setItem('notificationId', notificationId);
  };

  const cancelNotification = () => {
    const notificationId = localStorage.getItem('notificationId');
    if (notificationId) {
      clearTimeout(parseInt(notificationId));
      localStorage.removeItem('notificationId');
    }
  };

  const showNotification = () => {
    if (Notification.permission === 'granted') {
      new Notification(t('app.title'), {
        body: t('notification.body'),
        icon: '/logo192.png'
      });
    }
  };

  const handleToggle = () => {
    if (!notifications.daily && Notification.permission !== 'granted') {
      requestPermission();
    } else {
      setNotifications(prev => ({
        ...prev,
        daily: !prev.daily
      }));
      saveSettings(!notifications.daily, '09:00');
    }
  };

  const handleTimeChange = (e) => {
    const newTime = e.target.value;
    saveSettings(notifications.daily, newTime);
  };

  return (
    <div className="settings-section">
      <h2> </h2>
      <div className="settings-content">
        <div className="setting-item">
          <label>
            <input
              type="checkbox"
              checked={notifications.daily}
              onChange={handleToggle}
            />
               
          </label>
        </div>
        <div className="setting-item">
          <label>
            <input
              type="checkbox"
              checked={notifications.weekly}
              onChange={() => handleChange('weekly')}
            />
             
          </label>
        </div>
        <div className="setting-item">
          <label>
            <input
              type="checkbox"
              checked={notifications.email}
              onChange={() => handleChange('email')}
            />
             
          </label>
        </div>
      </div>
    </div>
  );
}

export default NotificationSettings; 